<template>
<div class='jf_detail'>
    <div class='head'>
        <div class="back"  @click.stop="back()">
            <img src="../../assets/image/联合42.png" alt="">
        </div>
        <div class="header">积分明细</div>
    </div>
    <v-page class="plan-list-page" style="font-size: 0.3rem;">
      <mt-loadmore ref="loadmore" class="loadmore"
                :top-method="loadTop" v-infinite-scroll="loadBottom" infinite-scroll-disabled="moreLoading01" infinite-scroll-distance="40" infinite-scroll-immediate-check="false" :autoFill="false">
                <div class="activity-list" ref="scrollContainer">
                    <div class='activity_list_top'>
                        <div class='activity_list_left'>总积分</div>
                        <div class='activity_list_right'>
                            <img src="../../assets/image/积分图片.png" alt="">
                            <span>{{yearJF}}</span>
                        </div>
                    </div>
                    <div class='activity_list_item' v-for="item1 in detailList" :key="item1.code">
                        <div class='item_top'>
                            <div class='item_top_left'>
                                <img src="../../assets/image/时间前.png" alt="">
                                <span>{{item1.time}}</span>
                            </div>
                            <div class='item_top_right'>
                                <span>积分</span>
                                <b>{{item1.countScore}}</b>
                            </div>
                        </div>
                        <div class='item_contain'>
                            <div class='item_contain_right' v-for="item2 in item1.value" :key="item2.code">
                                <div class='item_contain_right_list'>
                                    <div class='item_contain_right_list_left'>
                                        <div class='listTime'>{{item2.gainTime}}</div>
                                        <div class='listName'>
                                          <span class='listScore'>{{item2.reason}}</span>
                                          <span class='listCode' v-show='item2.specialCodeName != null'>{{item2.specialCodeName}}</span>
                                        </div>
                                    </div>
                                    <div class='item_contain_right_list_right'>
                                        +{{item2.gainScore}}
                                    </div>
                                </div>
                            </div>
                            <div class='item_contain_top_circle'></div>
                            <div class='item_contain_bottom_circle'></div>
                        </div>
                    </div>
                    <div class="more_loading" style="height:0.30rem; line-height:0.30rem; text-align:center" v-show="!queryLoading01">
                        <div type="snake" color="#00ccff" :size="20" v-show="moreLoading01 && !allLoaded01">加载中...</div>
                        <div v-show="allLoaded01" style="color:#ccc;margin-top:0.1rem;">---- 看到底线了 ----</div>
                    </div>
                </div>
            </mt-loadmore>

      <div class="list-empty-show" v-if="isEmpty">
        暂无数据
      </div>

    </v-page>
    <div class='empty'>
        <img src="../../assets/image/空空如也.png" alt="">
        <div class='emptyWord'>空空如也~</div>
    </div>
    <!-- 加载页面 -->
    <Loading :dialogVisible="verDialogVisible"></Loading>
</div>
</template>

<script>
import Loading from './../components/loading'
import API from '@/api/api'
import $ from 'jquery'
import { Indicator } from 'mint-ui'

export default {
  name: 'activity',
  components: {
    Loading
  },
  data () {
    return {
      yearJF: 0, // 本年积分
      isEmpty: false, // 列表查询结果是否为空
      allLoaded: false, // 列表数据是否全部加载
      topStatus: false, // 顶部下拉刷新
      queryLoading01: false,
      moreLoading01: false,
      allLoaded01: false,
      detailList: [], // 明细集合
      pageSize: 10,
      pageNum: 1,
      pageCount: 100,
      verDialogVisible: true // 加载页
    }
  },
  created () {
    this.$parent.isChange = 1
    // StatusBar.styleLightContent();
    this.init('down')
    this.getselectYearScore()
    $('.notData-all').hide()
  },
  mounted () {
    window.onresize = function () {
      if ($(document).height() < 400) {
        $('.activity-list').css('min-height', '5.4rem')
      } else {
        $('.activity-list').css('min-height', '11.12rem')
      }
    }
  },
  methods: {
    // 返回上一路由
    back () {
      this.$router.back()
    },
    // 下拉刷新
    loadTop () {
      this.pageNum = 1
      this.allLoaded = false
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      //   this.detailList = [];
      this.init('down')
      this.$refs.loadmore.onTopLoaded()
    },
    // 上拉加载
    loadBottom () {
      if (this.allLoaded01) {
        this.moreLoading01 = true
        return
      }
      if (this.queryLoading01) {
        return
      }
      this.moreLoading01 = !this.queryLoading01
      setTimeout(() => {
        this.pageNum++
        this.init('up')
      }, 500)
    },

    init (flag) {
      this.queryLoading01 = false
      this.moreLoading01 = false
      this.allLoaded01 = false
      const that = this
      API.post('findCreditsDetil', {
        pageNum: this.pageNum,
        pageSize: this.pageSize
      }, 'json', (data, err) => {
        if (!data.success) {
          alert(data.msg)
        } else if (data.success) {
          if (flag === 'down') {
            this.detailList = []
          }
          this.pageCount = data.data.pageCount
          that.forBackData(data.data.pageData)
          this.verDialogVisible = false
        }
      })
    },
    // 获取本年积分
    getselectYearScore () {
      API.post('selectYearScore', {
      }, 'json', (data, err) => {
        if (data.success) {
          this.yearJF = data.data
        } else {
          alert(data.status)
        }
      })
    },
    jiazai () {

    },
    // 循环返回的数据
    forBackData (list) {
      for (var i = 0; i < list.length; i++) {
        // 解析考试信息
        for (var k = 0; k < list[i].value.length; k++) {
          list[i].value[k].gainTime = this.changeTime(list[i].value[k].gainTime)
          list[i].countScore += Number(list[i].value[k].gainScore)
        }
        list[i].time = this.GLOBAL.changeTime2(list[i].time)
        this.detailList.push(list[i])
      }
      this.isEmpty = false
      this.allLoaded01 = this.detailList.length === this.pageCount || list.length === 1
      this.moreLoading01 = this.allLoaded01
      Indicator.close()
      if (this.detailList.length === 0) {
        $('.empty').css('display', 'flex')
        $('.plan-list-page').css('display', 'none')
      } else {
        $('.empty').css('display', 'none')
        $('.plan-list-page').css('display', 'block')
      }
    },
    // //转换日期格式
    changeTime (timeMes) {
      var oDate = new Date(timeMes)
      var oHour = oDate.getHours()
      var oMin = oDate.getMinutes()

      var oTime = this.GLOBAL.addZero(oHour) + ':' + this.GLOBAL.addZero(oMin)

      return oTime
    }
  }

}
</script>
<style scoped>
    .jf_detail{
        width:100%;
        height:100vh;
    }
    .jf_detail .head{
        width:100%;
        background:#fff;
        font-size:0.36rem;
        font-family: PingFang-SC-Medium;
        position: fixed;
        top:0;
        height: 0.94rem;
        display: flex;
        justify-content: center;
        z-index: 99;
        border-bottom: 0.012rem solid #F5F6F8;
    }
    .jf_detail .head .back{
        width:0.89rem;
        height:0.94rem;
        position: fixed;
        top:0;
        left:0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 100;
    }
    .jf_detail .head .back img{
        width:0.19rem;
        height:0.34rem;
    }
    .jf_detail .head .header{
        line-height: 0.94rem;
        font-size:0.38rem;
        color:#4E4E4E;
        width:100%;
        padding-top:0.02rem;
        white-space:nowrap;
        text-align: center
    }
    .activity-list{
        width: 100vw;
        height: calc(100vh - 0.94rem);
        padding:0 0.35rem;
    }
    .activity-list .activity_list_top{
        width:100%;
        height:0.92rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .activity-list .activity_list_top .activity_list_left{
        font-size:0.34rem;
        color:#4E4E4E;
    }
    .activity-list .activity_list_top .activity_list_right{
        display: flex;
        align-items: center;
    }
    .activity-list .activity_list_top .activity_list_right img{
        width:0.28rem;
        height:0.28rem;
        display: block;
        margin-right: 0.1rem;
    }
    .activity-list .activity_list_top .activity_list_right span{
        font-size:0.4rem;
        color:#FEA33B;
        font-weight: bold;
    }
    .activity-list .activity_list_item{
        width:100%;
        border-top:0.012rem solid #F3F3F3;
    }
    .activity-list .activity_list_item .item_top{
        width:100%;
        height:0.7rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .activity-list .activity_list_item .item_top .item_top_left{
        display: flex;
        align-items: center;
    }
    .activity-list .activity_list_item .item_top .item_top_left img{
        width:0.17rem;
        height:0.17rem;
        display: block;
        margin-right:0.07rem;
    }
    .activity-list .activity_list_item .item_top .item_top_left span{
        font-size: 0.3rem;
        color:#6E747E;
    }
    .activity-list .activity_list_item .item_top .item_top_right{
        display: flex;
        align-items: center;
    }
    .activity-list .activity_list_item .item_top .item_top_right span{
        font-size:0.28rem;
        color:#6E747E;
        margin-right: 0.07rem;
    }
    .activity-list .activity_list_item .item_top .item_top_right b{
        font-size: 0.38rem;
        color:#FEA33B;
        font-weight: normal;
    }
    .activity-list .activity_list_item .item_contain{
        width:100%;
        padding:0.2rem 0 0.3rem 0.075rem;
        position: relative;
    }
    .activity-list .activity_list_item .item_contain .item_contain_top_circle{
        width:0.13rem;
        height:0.13rem;
        background: #fff;
        border-radius: 50%;
        border:0.02rem solid #DEE6F2;
        position: absolute;
        top:0.17rem;
        left:0.015rem;
        z-index: 2;
    }
    .activity-list .activity_list_item .item_contain .item_contain_bottom_circle{
        width:0.13rem;
        height:0.13rem;
        background: #fff;
        border-radius: 50%;
        border:0.02rem solid #DEE6F2;
        position: absolute;
        bottom:0.27rem;
        left:0.015rem;
        z-index: 2;
    }
    .activity-list .activity_list_item .item_contain_right{
        border-left: 0.02rem solid #DEE6F2;
        padding-left:0.24rem;
        padding-top:0.32rem;
    }
    .activity-list .activity_list_item .item_contain_right:first-child{
        padding-top:0;
    }
    .activity-list .activity_list_item .item_contain_right .item_contain_right_list{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .item_contain_right_list .item_contain_right_list_left{
        display: flex;
        flex-direction: column;
    }
    .item_contain_right_list .item_contain_right_list_left .listTime{
        font-size:0.26rem;
        color:#9FA7AF;
        padding-bottom: 0.11rem;
    }
    .item_contain_right_list .item_contain_right_list_left .listName{
        font-size:0.28rem;
        color:#6E747E;
        padding-left:0.02rem;
    }
    .item_contain_right_list .item_contain_right_list_left .listName .listCode{
      display: inline-block;
      padding:0.08rem 0.12rem;
      background: rgba(87, 148, 246,0.18);
      border-radius: 0.05rem;
      color:#5794F6;
    }
    .item_contain_right_list .item_contain_right_list_right{
        display: flex;
        align-items: center;
        font-size:0.26rem;
        color:#FEA33B;
    }
    .mint-loadmore-top{
        font-size: 0.3rem;
    }
    .mint-loadmore{
        max-height: calc(100vh - 0.94rem);
        overflow-y: auto;
        margin-top: 0.94rem;
        background: #fff;

    }
    .empty{
        margin-top:2.75rem;
        display: none;
        flex-direction: column;
        width:100%;
        align-items: center;
    }
    .empty img{
        width:2.91rem;
        height:2.63rem;
    }
    .empty .emptyWord{
        margin-top:0.4rem;
        color:#9FA7AF;
        font-size:0.32rem;
    }
</style>



